.welcome-container {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .shop-categories {
    width: 50vw;
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
  
  .shop-categories h6 {
    cursor: pointer;
  }
  
  .shop-item {
    margin-top: 60px;
  }
  
  .shop-item h6:hover{
    font-size: 14px;
  }
  
  .welcome-logo {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  .welcome-logo img {
    width: 200px;
  }

.shop-item img {
    width: 200px;
  }