* {
  margin: 0;
  padding: 0;
  font-family: "NobelBook";
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f1f1f1;
}

.about-section {
  background: url(../../assets/images/landing3.jpg) no-repeat left;
  background-size: 65%;
  overflow: hidden;
  padding: 80px 0;
}

.about-page .inner-section {
  width: 55%;
  float: right;
  background-color: #fdfdfd;
  padding: 100px;
}
.about-page .inner-section h1 {
  margin-bottom: 20px;
  font-size: 1.4em;
  font-weight: 900;
}
.text {
  font-size: 0.9em;
  color: #545454;
  line-height: 30px;
  text-align: justify;
}

.core-values .content {
  display: flex;
  flex-direction: row;
  font-family: "NobelBook";
  color: #545454;
  font-size: 0.9em;
}

@media screen and (max-width: 1200px) {
  .inner-section {
    padding: 80px;
  }
}

@media screen and (max-width: 1000px) {
    .about-section{
        background-size: 100%;
        padding: 100px 40px;
    }
    .inner-section {
      width: 100%;
    }
  }

  @media screen and (max-width: 600px) {
    .about-section{
        padding: 0;
    }
    .inner-section {
      padding: 60px;
    }
  }

